/********* Mapa Student ************/

.mapStudent{
    background-image: url("../images/Fondo.jpg");
    background-repeat: no-repeat;
    background-size: 100% 93%;
    background-position: center bottom;
    padding: 10px;
    max-width: 100%;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.mapStudentBand{
    background-image: url("../images/Personaje_balanza.png"),url("../images/fondo-instrucciones.png") ;
    background-repeat: no-repeat;
    background-position: 0px 0px, 50px 20px;
    background-size: 182px 178px, auto 176px;
    max-width: 90%;
    height: 178px;
    position: relative;
}

.mapStudentText{
    font-size: 18pt;
    text-align: left;
    margin-left: 25%;
    padding-top: 15px;
    width: 650px;
}

