    padding-top: 20px;
}

.mapStudentInstructions{
    line-height: 80%;
    width: 40vw;
}

.mapStudentContainer{
    height: 500px;
    position: relative;
}

.mapLeftSide{
    position: absolute;
    left: 0;
    top: 0; 
