.background{
    background: navy
}
.draw{
    width: 90%;
    margin: 0 auto;
    position: relative;
}
* {
    margin: 0px;
    padding: 0px;
}

.literally .button-style-1 {
    border: 2px solid transparent;
    border-radius: 3px
}

.literally .button-style-1 {
    text-decoration: none;
    cursor: pointer
}

.literally .button-style-1.selected:not(.disabled) {
    background-color: transparent
}

.literally .button-style-1:hover:not(.disabled) {
    border-color: transparent
}

.literally .button-style-1.disabled {
    cursor: default;
    opacity: 0.3
}

.literally.toolbar-at-top .lc-drawing {
    bottom: 0;
    top: 31px
}

.literally.toolbar-at-top .lc-options {
    top: 0;
    border-bottom: 1px solid transparent
}

.literally.toolbar-at-bottom .lc-drawing {
    bottom: 31px;
    top: 0
}

.literally.toolbar-at-bottom .lc-options {
    bottom: 0;
    border-top: none;
    width: 33%;
    z-index: 9999;
    margin: 0 auto
}

.literally.toolbar-hidden .lc-drawing {
    left: 0;
    right: 0;
    bottom: 0;
    top: 0
}

.literally.toolbar-hidden .lc-options,
.literally.toolbar-hidden .lc-picker {
    display: none
}

.literally {
    position: relative;
    background-color: #0000;
    min-height: 100vh;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -ms-touch-action: none;
    user-select: none
}

.literally,
.literally * {
    box-sizing: border-box
}

.literally>* {
    position: absolute
}

.literally .lc-picker {
    bottom: 0;
    display: -webkit-flex;
    display: flex;
    height: 95px;
    width: 40%;
    background-color: transparent
}

.literally .lc-drawing {
    right: 0;
    left: 0;
    bottom: 0;
    left: 0
}

.literally .lc-drawing>* {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0
}

.literally .lc-drawing.with-gui {
    right: 0;
    left: 0px;
    cursor: default;
    height: 80vh;
}

.literally .lc-drawing.with-gui .polygon-toolbar {
    top: auto;
    height: 31px
}

.literally .lc-drawing.with-gui .polygon-toolbar .polygon-toolbar-button {
    float: left
}

.literally .lc-drawing.with-gui .text-tool-input:focus {
    outline: none
}

.literally .lc-picker {
    z-index: 1001;
    border-right: 1px solid transparent;
    position: absolute;
    bottom: 20px;
